.about__container {
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 2%;
    margin: auto;
    color: white;


}
.fswd{
    font-size: 22px;
}

.about__me {
    width: 60%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    /* transform: rotate(10deg);
    transition: var(--transition); */
    /* height: 400px; */
    height: 100%;
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: #323B55;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: #EFA61C;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: white;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 1rem;
}

.about__card small {
    font-size: 0.9rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
.about__content h3 {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
#address{
    color: #323b55e9;
}
#introself{
    color: #323b55e9;
}

@media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem; 
    }
    .about__me-image{
        height: 100%;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
    .eduDiv {
        margin-bottom: 2rem;
    }
    .eduDiv2{
        margin: auto;
        width: 70%;
        padding-left: 5%;
        /* padding-right: 5rem; */
        color: var(--color-primary);
        
    }
}
  
@media screen and (max-width: 600px){
    .about__me {
        width: 80%;
        margin: 0rem auto 3rem; 
        /* margin-left: 25px; */
        /* margin-right: 25px; */
        
    }
    .about__me-image{
        height: 100%;
    }
    .about__container{
        margin-bottom: -3%;
    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
    .eduDiv{
        display: flex;
        flex-direction: column;
        margin-bottom: 2rem;
    }
    .edu{
        margin-bottom: -10rem;

    }
    .edu p{
        background: var(--color-bg-variant);
        padding: 1.2rem;
        border-radius: 1.2rem;
        text-align: center;
        border: 1px solid transparent;
        transition: var(--transition);
    }
    .eduDiv2{
        width: 100%;
        padding-left: 10%;
        margin: auto;
        /* padding-left: 1rem;
        padding-right: 1rem; */
        color: var(--color-primary);
    }
    .fswd{
        font-size: 17px;
    }
    .masai{
        font-size: 14px;
    }
    .masai1{
        font-size: 5px;
    }
    .present{
        font-size: 13px;
    }
}