.contact__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    color:#323B55 ;
}
.intro{
    width: 80%;
    margin: auto;
    align-items: center;
}
