.contact__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid #323B55 ;
}

.contact__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
    background-color: #EFA61C;
}
.cta {
    display: flex;
    justify-content: center;
    gap: 1.2rem;
    margin-top: 1.5rem;
  }
  #CV:hover{
    background-color: #EFA61C;
    color: white;
    transition: 1.2s;
  }
  #talk:hover{
    background-color: #EFA61C;
    color: white;
    transition: 1.2s;
  }
  #img{
    border-radius: 15px;
    width: 400px;
  }
  
  @media screen and (max-width: 400px){
    .intro{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: -100px;
    }
    #img{
        border-radius: 15px;
        margin: auto;
        width: 200px;
        /* border: 1px solid blue; */
    }
    #imgDiv{
        margin-top: 12%;
        width: 95%;
        /* border: 1px solid red; */
        align-items: center;
        justify-content: center;
    }
  }

  @media screen and (max-width: 600px){
    .intro{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: -100px;
    }
    #img{
        border-radius: 15px;
        margin: auto;
    }
    #imgDiv{
        margin-top: 3%;
        width: 95%;
        /* border: 1px solid red; */
        align-items: center;
        justify-content: center;
    }
  }



  @media only screen and (max-width: 1024px) and (min-width: 400px){
    .intro{
        
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 90%;
    }
    #imgDiv{
        margin-top: 3%;
    }
    #img{
        width: 350px;
    }
  }