.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experience__container > div {
    background: #323B55;
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background:#323B55;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: white;
    font-weight: 600;
    font-size: 24px;
}

.experience__container > div h4 {
    color: white;
}
.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: #EFA61C;
}



.statsDiv{
    margin: auto;
    text-align: center;
}
.stat{
    width: 50%;
    /* margin: auto; */
    
}
.stat1{
    width: 50%;
    margin-left: 1rem;
    /* margin: auto; */
}

@media screen and (max-width: 1024px){
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        margin: 0 auto;
        padding: 2rem;
    }
    .experience__content{
       
        /* width: 70%;
        padding-left: 7%;
        margin: 0 auto; */
        width: 100%;
        padding-left: 20%;
        margin: 0 auto;
    }
    .experience__content p {
        padding: 1rem;
    }
    .githubCal{
        padding-left: 50px;
        padding-right: 50px;
    }
}
  
@media screen and (max-width: 600px){
    .experience__container {
        gap: 1rem;
    }
    .statsDiv{
        margin: auto;
        /* width: 120%; */
        text-align: center;
        justify-content: center;
    }
    .githubCal{
        padding-left: 10px;
        padding-right: 10px;
    }
    .stat{
        width: 350px;
        padding-left: 4%;
        padding-right: 4%;
        /* margin-left: 15px; */
    }
    .stat1{
        width: 350px;
        margin: 0;
        padding-left: 4%;
        padding-right: 4%;
        /* width: 60%; */
        /* margin-left: 15px; */
    }
    .statInner{
        display: flex;
        flex-direction: column;
    }
    

    .experience__container > div {
        width: 88%;
        padding: 2rem 1rem;
    }
    .githubCal{
        padding-left: 20px;
        padding-right: 20px;
    }
    .experience__content{
        /* width: 80%;
        margin-left: 5%;
        margin: auto; */
        width: 100%;
        padding-left: 10%;
        margin: 0 auto;
    }
}