.portfolio__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
}

.portfolio__item {
    background: #323B55;
    padding: 1.3rem;
    border-radius: 1.5rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: #323B55;
    background: #323B55;
}

.portfolio__item-image {
    /* border-radius: 1.5rem; */
    overflow: hidden;
    /* height: 42%; */
    /* width: 500px; */
    /* height: 300px;  */
    margin-bottom: 8%;
    border-radius: 12px;
}
.website-img{
    object-fit:fill;
}
.website-img{
    height: 80%;  
}

.portfolio__item h3 {
    margin: 1rem 0;
    color: white;
    font-weight: 600;
}

.portfolio__item p {
    margin: 0 0 1.2rem 0;
    font-size: 0.9rem;
    color: white;
}


.portfolio__item-cta {
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem; 
    margin-top: -1rem;
    
    justify-content: space-around
}
#git:hover{
    background-color: #EFA61C;
    color: white;
    transition: 1.2s;
}

#git {
    background-color: #EFA61C;
    color: white; 
}
#live:hover{
    background-color: #EFA61C;
    color: white; 
    
}
#live {
    background-color: #EFA61C;
    color: white; 
    
}
.portfolio__item-content {
    /* height: 50%; */
    margin-bottom: 50px;
}

@media screen and (max-width: 1024px){
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
    .website-img{
        object-fit:fill;
    }
    
}
  
@media screen and (max-width: 600px){
    .portfolio__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .portfolio__item-image{
        height: 300px;
        margin-bottom: -10%;
        
    }
    .website-img{
        object-fit:fill;
    }
    
    .portfolio__item h3 {
        margin-top: 10px;
        margin-bottom: 10px;
    } 
    .portfolio__item-cta {
        
        gap: 1rem;
        margin-top: 1 rem;
        
    }
    .stack{
        padding-top: 10px;
    }
}