@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap');
#logo{
    font-family: 'Black Ops One', cursive;
    font-size: 30px;
}
.logo a{
  color: #323B55;
}
.cta {
    display: flex;
    justify-content: center;
    gap: 1.2rem;
    margin-top: 2.5rem;
  }
  
.topbar{
    /* padding-left: 192px; */
    align-items: center;
    padding-left: 10%;
    /* padding-right: 192px; */
    padding-right: 10%;
    display: flex;
    padding-top: 10px;
    padding-bottom: 10px;
    position: fixed;
    width: 100%;
    /* background-color: #2c2c6c;; */
    background-color: #F0F8FF;
}

.navbar a {
    font-size: 14px;
    padding: 0.9rem;
    border-radius: 10%;
    display: flex;
    /* color: var(--color-light); */
    color: #323B55;
    font-size: 1.1rem;
  }

.navbar a:hover {
    background: radial-gradient(circle at 10% 20%, rgb(228, 118, 0) 0%, rgb(247, 189, 2) 90%);
    color: white;
  }


.navbar a.active {
    /* background: var(--color-light); */
    background: radial-gradient(circle at 10% 20%, rgb(228, 118, 0) 0%, rgb(247, 189, 2) 90%);
    color: white;
  }


  @media screen and (max-width: 600px){
    .topbar{
      display: none;
    }
}

@media screen and (max-width: 1024px){
    .topbar{
        padding-left: 20px;
        padding-right: 20px;
    }
    .navbar a {
        font-size: 12px;
        padding: 0.9rem;
        border-radius: 10%;
        display: flex;
        /* color: var(--color-light); */
        color: black;
        font-size: 1.1rem;
      }
      .logo{
        display: none;
      }
    
}